@import url(header.css);
@import url(main.css);
@import url(footer.css);

@font-face {
  font-family: "PT Sans - Narrow Bold", sans-serif;
  src: url("./../fonts/pt-sans-narrow-bold.ttf") format("opentype");
  font-style: normal;
  font-weight: 400;
}
*, *:after, *:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  margin: 0 ;
}





